
body{
font-family: Open Sans, Tahoma, sans-serif;
}

h1, h2, p{
  margin: 0;
  padding: 0
}

.app-logo{
  margin: 40px auto 30px auto;
  text-align: center;
}

.container{
  display: flex;
  justify-content: space-between;
  width: 850px;
  margin: 0 auto;
}

.sidebar{
  width: 28%;
  background-color: white;
  padding: 20px 0px 20px 0px;
  margin-right: 15px;
  box-sizing: border-box;
  box-shadow: 0 0 3px 0 hsla(0, 0%, 62%, .5);
}

.sidebar_title{
  font-weight: 400;
  font-size: 12px;
  margin-bottom: 20px;
  text-transform: uppercase;
  margin-left: 20px;
}

.sidebar_currency-btns{
  margin-bottom: 40px;
  margin-left: 20px;
  margin-right: 20px;
}

.sidebar_currency-btn{
  display: none;
}

.sidebar_currency-label{
  box-sizing: border-box;
  width: 33%;
  padding: 10px 20px 10px 20px;
  cursor: pointer;
  border: 1px solid #8b9497;
  font-size: 12px;
}
.sidebar_currency-label:hover{
  background-color: #f2fcff;
  border: 1px solid #2196f3;
  color:  #8b9497;
}
.sidebar_currency-btn:checked +.sidebar_currency-label{
  border: 1px solid #2196f3;
  background-color: #2196f3;
  color: white;
}

.sidebar_currency-label--usd{
  border-radius: 0px;
}
.sidebar_currency-label--rub{
  border-radius: 5px 0px 0px 5px;
}
.sidebar_currency-label--eur{
  border-radius: 0px 5px 5px 0px;
}

.sidebar_changing-block{
  position: relative;
}

.sidebar_changing-block:hover{
  background-color: #f1fcff;
}

.sidebar_changing-checkbox{
  display: none;
}

.sidebar_changing-label{
  display: block;
  margin-left: 50px;
  padding-top: 8px;
  padding-bottom: 8px;
  font-size: 12px;
  position: relative;
  cursor: pointer;
}

.sidebar_changing-label::before{
  display: block;
  content: "";
  position: absolute;
  height: 17px;
  width: 17px;
  top: 7px;
  left: -30px;
  border: 1px solid #8b9497;
  border-radius: 3px;
  cursor: pointer;
}

.sidebar_changing-check:hover{
  border: 1px solid #2196f3;
  background-color: white;
}
.sidebar_changing-checkbox:hover + .sidebar_changing-block {
  border: 1px solid #2196f3;
}

.sidebar_changing-checkbox:checked + .sidebar_changing-label{
  color: #2196f3;
}
.sidebar_changing-checkbox:checked + .sidebar_changing-label::before{
  color: #2196f3;
  border-color: #2196f3;
  content: "âœ“";
  text-align: center;
}

.ticket-list{
  width: 69%
}

.flight{
    background-color: white;
    padding: 20px 20px 20px 20px;
    display: flex;
    margin-bottom: 20px;
    box-shadow: 0 0 3px 0 hsla(0, 0%, 62%, .5);
}
.flight_left-side{
  padding-right: 22px;
}
.flight_right-side{
  display: flex;
  justify-content: space-between;
  padding-left: 22px;
  border-left: 1px solid #8b9497;
  height: 122px;
  width: 85%;
  box-sizing: border-box;
}
.flight_avia-logo{
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 10px;
}

.flight-btn{
  background-color: #ff6d00;
  color: white;
  border-radius: 5px;
  box-shadow: 0 1px 0 0 #d64d08, 0 2px 1px 0 rgba(0, 0, 0, .1);
  padding: 7px 39px 7px 39px;
  outline: none;
  border: none;
  box-sizing: border-box;
  width: 160px;
  height: 60px;
  font-size: 18px;
}

.flight-btn:hover {
  border-bottom-color: transparent;
  background-color: #faa760;
  -o-transition: all .3s;
  transition: all .3s
  }

.avia-logo{
  width: 125px;
}

.flight_time{
  font-size: 33px;
  font-weight: 400;
  color: #4a4a4a;
  display: block;
  margin-bottom: 5px;
}

.flight_aeroport-block{
  font-size: 13px;
  font-weight: 400;
  margin-bottom: 10px;
  color: #4a4a4a;
}

.flight_data-block{
  font-size: 11px;
  color: #8b9497;
}

.flight_stops{
  font-size: 11px;
  color: #8b9497;
  padding-top: 10px;
  display: block;
  padding-left: 5px;
  padding-right: 5px;
  position: relative;
  text-transform: uppercase;
  }

  .flight_stops::after{
    display: block;
    content: "";
    position: absolute;
    height: 1px;
    width: 105px;
    top: 35px;
    left: -5px;
    background-color: #8b9497;
  }
